export const debounce = (f, delay) => {
    let timeout;
    return () => {
        if (timeout)
            clearTimeout(timeout);
        timeout = setTimeout(() => f(), delay);
    };
};
export const once = (f) => {
    let called = false;
    return (...args) => {
        if (!called)
            f(...args);
        called = true;
    };
};
export class Batcher {
    constructor(executor) {
        this.executor = executor;
        this.items = [];
    }
    async process() {
        const toProcess = this.items;
        this.items = [];
        await this.executor(toProcess.map(({ item }) => item));
        toProcess.map(({ onProcessed }) => onProcessed());
        if (this.items.length) {
            this.ongoing = this.process();
        }
        else {
            this.ongoing = undefined;
        }
    }
    async queue(item) {
        const result = new Promise((resolve) => this.items.push({ item, onProcessed: resolve }));
        if (!this.ongoing)
            this.ongoing = new Promise((resolve) => setTimeout(resolve, 15)).then(() => this.process());
        return result;
    }
}
